@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App-landing-pic {
  display: block;
  height: 45vmin;
  margin: 5rem auto;
}

.menuBar {
  padding: 0 1rem;
  border-bottom: solid 1px #e8e8e8;
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1;
  height: 5rem;
}

.logo {
  width: auto;
  float: left;
}

.logo > img {
  width: 5rem;
  height: auto;
  padding-top: 15px;
}

.menuCon {
  width: calc(100% - 115px);
  /* width: 90%; */
  float: left;
  padding: 1rem 0;
  /* background-color: pink; */
}

.menuCon .ant-menu-item {
  padding: 0px 5px;
}

.menuCon .ant-menu-submenu-title {
  padding: 10px 20px;
}

.menuCon .ant-menu-item a,
.menuCon .ant-menu-submenu-title a {
  padding: 10px 15px;
}

.menuCon .ant-menu-horizontal {
  border-bottom: none;
}

.menuCon .leftMenu {
  float: left;
}

.menuCon .rightMenu {
  float: right;
}

.barsMenu {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
}

.barsBtn {
  display: block;
  width: 20px;
  height: 2px;
  background: #1890ff;
  position: relative;
}

.barsBtn:after,
.barsBtn:before {
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: #1890ff;
}

.barsBtn:after {
  top: auto;
  bottom: -6px;
}

.barsMenu > span {
  display: block;
}

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: block;
  width: 100%;
}
.ant-drawer-body > a {
  display: block;
  padding: 2rem;
  font-size: 1rem;
  transition: ease-in-out 100ms;
}
.ant-drawer-body :hover {
  color: #0050b3;
}

@media (max-width: 740px) {
  .barsMenu {
    display: inline-block;
  }

  .leftMenu,
  .rightMenu {
    display: none;
  }

  .logo a {
    margin-left: -20px;
    padding: 1.5rem 2.75rem;
  }
}

.container {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}

.landing-info {
  display: block;
  position: -webkit-sticky;
  position: sticky;
  text-align: center;
}

.landing-pic {
  /* max-width: 400px; */
  width: 100%;
  height: 400px;
  margin: auto;
  display: block;
  -webkit-transform: translate(0, 2.5%);
          transform: translate(0, 2.5%);
  z-index: -1;
}

.main-image {
 background-position: center !important;
background-repeat: no-repeat !important;
background-color: black !important;
background-size: cover !important;
height: 500px;
width: 100%;
position: relative;
}

.landing-page-posters {
  transition: opacity 250ms linear, -webkit-transform 350ms cubic-bezier(0.5, 0, 0.5, 1);
  transition: transform 350ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear;
  transition: transform 350ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear, -webkit-transform 350ms cubic-bezier(0.5, 0, 0.5, 1);
}

.landing-page-posters:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);

}
